/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --theme-black-color: #28282b;
  --theme-yellow-color: #fdda0d;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Example of importing and using a custom font */
@font-face {
  font-family: "Segoe UI";
  src: url("./assets/fonts/NetflixSans_Rg.woff2") format("woff2"), url("./assets/fonts/NetflixSans_Md.woff2") format("woff2"), url("./assets/fonts/NetflixSans_Bd.woff2") format("woff2");
}

body {
  font-family: "Segoe UI" !important;
}
